body{
    margin: 0;
    width: 100%;
    height: 80vh;
}

.container-main{
    background-color: #333545;
    width: 100%;
    height: 595px;
    position: relative;
    display: flex;
}

.main-title{
    font-size: 45px;
    color: white;
    font-weight: bolder;
    margin-left: 55px;
    margin-top: 140px;
    width: 50%;
}

.main-title .allam{
    color: #ffae28;
}

.container-main .border-bottom{
    border-bottom: 5px solid #ffae28;
    width: 30%;
    height: 10%;
    /* background-color: red; */
