    margin-top: 23%;
    margin-left: -49%;
}

.contact{
    width: 10%;
    height: 8%;
    margin-top: 33%;
    border: none;
    padding: 5px;
    margin-left: -30%;
    border-radius: 25px;
    background-color: #ffae28;
    color: white;
    font-size: 15px;
    font-weight: bolder;
    cursor: pointer;
    transition: 0.5s;
}

.contact:hover{
    background-color: white;
    color: #ffae28;
}

.container-main .shape-wrapper{
    width: 40%;
