@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

body{
    margin: 0;
    width: 100%;
    height: 100vh;
    font-family: Poppins, sans-serif;
}

nav{
    background-color: #333545;
    /* buat main #262535 */
    /* buat kotak #424355 */
    color: white;
    padding: 5px;
    position: relative;
}

nav .container-nav{
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: red; */
    position: relative;
}

.container-nav .name{
    margin-right: 42%;
    width: 50%;
    /* background-color: yellow; */
    position: absolute;
    font-weight: bolder;
    font-size: 25px;
    cursor: pointer;
}

nav .container-nav ul{
    display: flex;
    list-style: none;
    margin-left: 60%;
}

nav .container-nav ul li{
    padding: 5px;
}

nav .container-nav ul li a{
    text-decoration: none;
    color: white;
    margin: 25px;
    transition: 0.8s;
}

.container-nav ul li a:hover{
    color: #ffae28;
}